@tailwind base;
@tailwind components;
@tailwind utilities;

/* Animation du gradient fluide */
@keyframes gradientFlow {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

.animate-gradient {
  background: linear-gradient(
    124deg,
    #AFCBFF,
    #5c76a6,
    #AFCBFF,
    #738ec1,
    #3d5681,
    #637dac,
    #486396,
    #809bd2
  );
  background-size: 300% 300%;
  animation: gradientFlow 30s ease infinite;
}
